import { use } from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { Link } from 'react-router-dom';
import React, { useEffect } from 'react';
import GlobalContext from '../context/context';
function Navigation() {
  const {data,setData} = React.useContext(GlobalContext);
  const[categories,setCategories] =  React.useState([]);
  const getCategories = async() =>{
    const url = "https://dummyjson.com/product/categories";
    const rs = await fetch(url);
    const data = await rs.json();
    setCategories(data);
  }
  useEffect(()=>{
    getCategories();
  },[]);
  return (
    <Navbar expand="lg" className="bg-body-tertiary">
      <Container>
        <Navbar.Brand as={Link} to="/">y l 1 Navbar</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/Cart">Cart({data.cart.length})</Nav.Link>
            <Nav.Link as={Link} to="/Forecast">Forecast</Nav.Link>
            <NavDropdown title="Categories" id="categories-nav-dropdown">
              {categories.map((e, i) => (
                <NavDropdown.Item as={Link} to={"/category/" + e.slug} key={i}>
                  {e.name}
                </NavDropdown.Item>
              ))}
            </NavDropdown>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Bm Th</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">
                Bm tip
              </NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Th tip</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">
                Ht th
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
} 

export default Navigation;